@font-face {
  font-family: "Space Grotesk";
  src: url("./fonts/SpaceGrotesk-VariableFont_wght.ttf")
    format("truetype-variations");
  font-weight: 400 900;
  font-stretch: 75% 125%;
}
// Colors
$black: rgb(21, 21, 21);
$green: rgb(78, 255, 160);
$dark-grey: rgb(36, 36, 36);
$light-grey: rgb(217, 217, 217);
$white: rgb(255, 255, 255);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Space Grotesk";
  font-weight: 500;
  color: $white;
}
body {
  background-color: $black;
  overflow-x: hidden;
}

h1 {
  font-weight: 900;
  font-size: 88px;
  line-height: 88px;
  letter-spacing: -2.5px;
}

h2 {
  font-weight: 900;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -1.5px;
}

h3 {
  font-weight: 900;
  font-size: 24px;
  line-height: 32px;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}
.column {
  flex-direction: column;
}

.justify-center {
  justify-content: center;
}
.justify-between {
  justify-content: space-between;
}
.justify-end {
  justify-content: end;
}

.items-center {
  align-items: center;
}
ul {
  list-style: none;
}

.gap-1 {
  gap: 4px;
}

.gap-2 {
  gap: 8px;
}

.gap-3 {
  gap: 16px;
}

.gap-4 {
  gap: 24px;
}

.gap-5 {
  gap: 32px;
}
a,
button {
  font-size: 16px;
  text-transform: uppercase;
  line-height: 26px;
  letter-spacing: 2.29px;
  border-bottom: 2px solid $green;
  padding-bottom: 10px;
  text-decoration: none;
}
button {
  background: none;
  border-top: none;
  border-left: none;
  border-right: none;
}

button:hover {
  color: $green;
  cursor: pointer;
}
form {
  width: 100%;
  textarea,
  input {
    width: 100%;
    background-color: transparent;
    border: none;
    outline: none;
    border-bottom: 1px solid $light-grey;
    padding: 16px;
    margin-bottom: 16px;
  }
  textarea::placeholder,
  input::placeholder {
    font-size: 16px;
    line-height: 26px;
    letter-spacing: -0.22px;
  }
  textarea {
    z-index: 1;
    height: 130px;
  }
  button {
    margin-top: 16px;
  }
  p.succes-message {
    margin-top: 32px;
    font-size: 16px;
    line-height: 26px;
    color: $green;
    border: 1px solid $green;
    padding: 24px;
  }
}
